@tailwind base;
@tailwind components;
@tailwind utilities;

html, h1, h2, h3, h4, h5, h6, p, a, span, html>body, html>body>* {
  font-family: 'Work Sans', sans-serif;
  color: #000;
}

button.fa-solid.fa-trash {
  color: var(--contrast);
  background-color: transparent;
  border: transparent;
  margin-bottom: 0;
  width: auto;
}

header {
  text-decoration: none;
}

header a {
  color: var(--contrast);
}

.collapse-arrow .collapse-title:after {
  left: 10rem;
}

.collapse-content {
  padding-left: 0;
  padding-right: 0;
}
