/* overwriting tailwind collapse */
.collapse.collapse-arrow {
  visibility: visible
}

.stats {
  color: #000;
}

.stat-shadow {
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
}

.badge {
  border-radius: 1em;
  color:#000
}

