.drawer-shadow {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.Tabletop {
  background-color: #bebdf7;
  border: #bebdf7 solid 1px;
}

.Lifestyle {
  background-color: #adeabc;
  border: #adeabc solid 1px;
}

.Editorial {
  background-color: #9bd9fe;
  border: #9bd9fe solid 1px;
}

.Flatlay {
  background-color: #ffeb99;
  border: #ffeb99 solid 1px;
}

.Ecomm {
  background-color: #fab6e3;
  border: #fab6e3 solid 1px;
}

.Other {
  background-color: transparent;
  border: #000 solid 1px;
}

a.active {
  background-color: rgba(31, 41, 55, 0.1);
  color: hsl(var(--bc) / var(--tw-text-opacity));
}

.schedule,
.todo,
.pastdue,
.completed,
.inprogress {
  @apply rounded-full border-solid border-2 px-2 text-xs tracking-wide uppercase whitespace-nowrap 
}

.schedule,
.todo,
.inprogress {
  @apply border-violet-600
}

.todo {
  @apply bg-violet-600 text-white
}

.schedule,
.inprogress {
  @apply text-violet-600
}

.completed {
  @apply border-emerald-600 text-emerald-600
}

.pastdue {
  @apply border-rose-600 text-rose-600
}

#card1,
#card2,
#card3,
#card4,
#card5 {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}